<template>
  <v-layout
    v-bind="binding"
  >
    <input
      ref="file"
      type="file"
      style="display: none"
      multiple
      @change="hideInputDialog"
    >
    <v-flex
      md3
    >
      <v-btn
        tile
        depressed
        :height="btnHeight"
        :disabled="disabled"
        class="ma-0"
        block
        @click="openInputDialog"
      >
        <v-icon>
          mdi-paperclip
        </v-icon>
        Anexar
      </v-btn>
    </v-flex>
    <v-flex>
      <v-list
        class="ma-0 pa-0"
      >
        <v-list-tile
          v-for="(item, i) in items"
          :key="`${item.name}-${i}`"
          :style="i !== items.length - 1 ? 'border-bottom: 0.5px solid grey' : ''"
        >
          <v-list-tile-content>
            <v-list-tile-title
              style="white-space: normal;"
            >
              <span>
                {{ item.name }}
              </span>
              ({{ item.size | bytes2human }})
            </v-list-tile-title>
          </v-list-tile-content>

          <v-list-tile-action>
            <v-icon
              color="red"
              @click="deleteFile(item)"
            >
              disabled_by_default
            </v-icon>
          </v-list-tile-action>
        </v-list-tile>
      </v-list>
    </v-flex>
    <!--v-flex>
      <v-layout
        column
      >
        <v-flex
          v-for="(item, i) in items"
          :key="`${item.name}-${i}`"
        >
          <v-chip
            class="ma-1"
            close
            label
            @input="deleteFile(item)"
          >
            <span
              class="text-truncate mr-1"
              style="max-width: 150px"
            >
              {{ item.name }}
            </span>
            ({{ item.size | bytes2human }})
          </v-chip>
        </v-flex>
      </v-layout>
    </v-flex-->
    <!--div
      style="width: 100%"
      class="d-flex flex-column"
    >
      <v-chip
        v-for="(item, i) in items"
        :key="`${item.name}-${i}`"
        class="ma-1"
        close
        label
        @input="deleteFile(item)"
      >
        <span
          class="text-truncate mr-1"
          style="max-width: 150px"
        >
          {{ item.name }}
        </span>
        ({{ item.size | bytes2human }})
      </v-chip>
    </div-->
  </v-layout>
</template>

<script>
  export default {
    props: {
      value: {
        type: Array,
        default: () => [],
      },
      disabled: {
        type: Boolean,
        default: false,
      },
      btnHeight: {
        type: Number,
        default: 40,
      },
    },

    data: () => ({
      itemsPerPage: 5,
      files: [],
      filesChecked: {},
      headers: [
        { text: 'Arquivo', value: 'name' },
        { text: 'Tamanho', value: 'size', align: 'end', width: '100px' },
        { text: '', value: 'action', sortable: false, align: 'end', width: '83px' },
      ],
    }),

    computed: {
      items () {
        return this.files.map(item => {
          return {
            name: item.name,
            size: item.size,
          }
        })
      },
      binding () {
        const binding = {}
        // if (this.$vuetify.breakpoint.mdAndDown) binding.column = true
        binding.column = true
        return binding
      }
    },

    watch: {
      files: function (val) {
        this.$emit('input', val)
      },

      value (newValue) {
        this.files = newValue
      },
    },

    methods: {
      openInputDialog () {
        this.$refs.file.click()
      },

      hideInputDialog (value) {
        let tmpFiles = [...value.target.files]
        tmpFiles = tmpFiles.filter(item => {
          if (!this.filesChecked[item.name]) {
            this.filesChecked[item.name] = true
            return true
          }
          return false
        })
        this.files = [...tmpFiles, ...this.files]
      },

      deleteFile (item) {
        this.filesChecked[item.name] = false
        const index = this.files.findIndex(it => it.name === item.name)
        if (this.files[index]) {
          this.files.splice(index, 1)
        }

        if (this.files.length === 0) {
          this.files = []
          this.filesChecked = {}
        }
      },
    },
  }
</script>

<style>
.wrap-text {
  -webkit-line-clamp: unset !important;
}
</style>
